.clientPage_wrapper {
  background-color: var(--color-3);
  padding-top: 70px;
}
.show_body_btn_wrapper {
  display: flex;
  align-items: center;
  background-color: var(--color-2);
  justify-content: space-between;
  color: var(--color-3);
  padding: 3px;
  margin-bottom: 10px;
  position: sticky;
  top: 50px;
  z-index: 2;
}
.body_count {
  padding: 4px;
  border-right: 2px solid var(--color-3);
  min-width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.show_body_btn {
  background-color: transparent;
  height: 35px;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.show_body_btn svg {
  fill: var(--color-3);
  transition: 0.2s;
  transform: rotate(-90deg);
}
.show_body_btn.open svg {
  transform: rotate(90deg) translateX(2px);
}

.compare_btn_active {
  position: absolute;
  top: 3px;
  left: 3px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: var(--color-6);
}
