.body_item_wrapper {
  margin-bottom: 10px;
  position: relative;
}
.body_btn_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-8);
  padding: 5px;
  gap: 30px;
  position: sticky;
  top: 90px;
}
.body_btn_wrapper_groupAccompany {
  background-color: var(--color-1);
}
.body_btn_wrapper_individualAccompany {
  background-color: var(--color-6);
}
.body_btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 30px;
  cursor: pointer;
  background-color: transparent;
  color: var(--color-3);
  border-right: 2px solid var(--color-3);
  padding: 7px;
}

.body_btn svg {
  fill: var(--color-3);
  transition: 0.2s;
  transform: rotate(-90deg);
}
.body_btn.open svg {
  transform: rotate(90deg) translateX(2px);
}

.data_wrapper {
  display: flex;
  align-items: center;
  font-size: 14px;
  gap: 10px;
}
.body_control_wrapper {
  display: flex;
  gap: 10px;
  position: relative;
}
.btn_compare_alert {
  position: absolute;
  left: -28px;
  top: 2px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-3);
  color: var(--color-2);
}
.body_control_btn_compare {
  padding-right: 5px;
  padding-left: 5px;
  background-color: transparent;
  border: 1px solid var(--color-3);
  color: var(--color-3);
  cursor: pointer;
}
.body_control_delete_btn {
  padding: 5px 5px 5px 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background-color: transparent;
}

.body_info {
  border: solid 2px var(--color-8);
  margin-bottom: 10px;
}
.body_info_groupAccompany {
  border-color: var(--color-1);
}
.body_info_individualAccompany {
  border-color: var(--color-6);
}
.body_item {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  background-color: var(--color-4);
}
.body_item:not(:last-child) {
  margin-bottom: 5px;
}
.body_item_title {
  width: 50%;
}
.body_item_input {
  width: 50%;
  height: 30px;
  padding: 0 10px;
  display: inline-block;
}
.loader_wrapper {
  position: relative;
  min-height: 40px;
}
.body_empty {
  padding: 10px;
  display: flex;
  justify-content: center;
}

.body_time {
  font-size: 12px;
}
