.restart_wrapper {
  position: fixed;
  bottom: 50px;
  right: 10px;
  background-color: var(--color-7);
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  cursor: pointer;
}
.restart_wrapper button {
  line-height: 1;
  background-color: transparent;
  cursor: pointer;
}
.restart_wrapper span {
  display: inline-block;
  border-left: 1px solid var(--color-3);
  height: 25px;
  padding-left: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  line-height: 1;
}
