.alert_fon {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(2.5px);
  padding-left: 20px;
  padding-right: 20px;
}

.alert_wrapper {
  background-color: var(--color-1);
  padding: 30px;
  color: var(--color-3);
  border-radius: 5px;
  min-height: 200px;
  min-width: 300px;
  max-width: 400px;
  box-shadow: 0px 26px 43px -28px #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 30px;
}
.alert_text {
  text-align: center;
}
.alert_close_btn {
  padding: 5px 15px;
  background-color: var(--color-3);
  border-radius: 5px;
  color: var(--color-1);
}
