.faceInputChecked {
  width: 30px;
  height: 30px;
  border: 2px solid var(--color-1);
  cursor: pointer;
}

.faceInputChecked.active {
  background-color: var(--color-1);
}

.item_grup {
  background-color: var(--color-4);
  margin-bottom: 10px;
  border: solid 1px var(--color-1);
}
.item_grup_item {
  position: relative;
  padding: 10px;
  background-color: var(--color-4);
  display: flex;
  justify-content: space-between;
  align-items: center;

  gap: 5px;
}
