.fon {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff6f;
  backdrop-filter: blur(17.5px);
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}
.close_popup_btm {
  position: fixed;
  top: 10px;
  right: 10px;
  /* background-color: #00000028; */
  background-color: var(--color-1);
  fill: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
}
.compare_wrapper {
  display: grid;
  grid-template-rows: 20px 20px repeat(7, 30px);
  grid-template-columns: min-content repeat(3, auto);
  /* grid-template-columns: min-content auto auto 60px; */
  gap: 2px;
  grid-auto-flow: column;
  font-size: 12px;
  align-items: center;
}

.body_item_title {
  white-space: nowrap;
  padding-right: 5px;
}
.body_element {
  padding-left: 5px;
  padding-right: 5px;
  border: solid 1px var(--color-1);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.body_element.plus {
  font-weight: 600;
  color: green;
}
.body_element.minus {
  color: red;
  font-weight: 600;
}
