.formInner {
  padding: 20px;
  width: 100%;
  max-width: 350px;
}
.form_input,
.form_btn {
  display: block;
  width: 100%;
  padding: 0 20px;
  margin-bottom: 10px;
  background: var(--color-5);
  height: 45px;
  border-width: 0;
  border-radius: 20px;
}

.form_btn {
  margin-top: 30px;
  background: var(--color-2);
  border-bottom: 4px solid var(--color-1);
  color: white;
  font-size: 14px;
  cursor: pointer;
  transition: 0.1s;
}
.form_btn:hover {
  background: var(--color-1);
  border-bottom: 4px solid var(--color-2);
}
.form_btn:active {
  background: var(--color-2);
}
