.compare_btn {
  padding: 5px;
  cursor: pointer;
  min-width: 40px;
  height: 40px;
  background-color: var(--color-2);
  color: var(--color-3);
  position: relative;
}
.compare_btn_active {
  position: absolute;
  top: 3px;
  right: 3px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: var(--color-6);
}
