.btn {
  width: 100%;
  border: solid 2px var(--color-2);
  cursor: pointer;
  max-width: 400px;
  box-shadow: lightgray 0px 1px;
  transition: all 150ms ease-in-out 0s;
  margin-top: 10px;
}

.btn:hover {
  box-shadow: rgb(232, 236, 235) 0px 0px 0px 3px;
  border-color: var(--color-1);
}
.btn_content_wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
