.search {
  display: flex;
  align-items: center;
  position: relative;
}
.search__btn {
  padding: 5px;

  line-height: 1;
  background-color: transparent;
  cursor: pointer;
}
.search__block {
  position: absolute;
  top: -2px;
  right: -4px;
  height: 100%;
}
.search__input {
  height: 40px;
  position: absolute;
  display: none;
  top: 0;
  right: 0;
  color: #000;
  width: 0%;
  border-radius: 20px;
  outline: none;
}
.search__btn_search {
  height: 40px;
  width: 40px;
  display: none;
  position: relative;
  z-index: 2;
  background-color: var(--color-2);
  border-radius: 50%;
}
.search__btn_search svg {
  fill: var(--color-3);
}
.search.active .search__btn_search {
  display: flex;
  align-items: center;
  justify-content: center;
}
.search.active .search__input {
  border: solid 2px var(--color-2);
  transition: width 0.2s, padding 0.2s;
  padding: 0 2.8125rem 0 0.9375rem;
  width: 280px;
  display: block;
}
