.sort_wrapper {
  position: relative;
  margin-bottom: 10px;
}
.sort_btn {
  position: relative;
  background-color: var(--color-3);
  /* color: var(--color-3); */
  border: solid 2px var(--color-1);
  width: 100%;
  height: 35px;
  padding-right: 30px;
  padding-left: 10px;
  cursor: pointer;
}
.sort_btn svg {
  position: absolute;
  top: 5px;
  right: 10px;
  fill: var(--color-1);
  transition: 0.2s;
  transform: rotate(-90deg);
}

.sort_btn.open {
  z-index: 21;
  border: none;
}
.sort_btn.open .sort_btn_value {
  border-bottom: 2px solid var(--color-1);
}
.sort_btn.open svg {
  transform: rotate(90deg) translateX(2px);
  z-index: 21;
}

.sort_content_wrapper {
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 21;
  background-color: var(--color-3);
  width: 100%;
  padding: 20px;
  box-shadow: 0px 26px 43px -28px #000000;
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: calc(100vh - 110px);
  overflow: auto;
}

.sort_item {
  padding: 10px 20px;
  border: solid 2px var(--color-1);
  cursor: pointer;
}
.sort_item.active {
  background-color: var(--color-1);
  color: var(--color-3);
}
.sort_fon {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #00000073;
  backdrop-filter: blur(1.5px);
  z-index: 20;
}
