.adaptiveImage {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
}
.adaptiveImage img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover !important;
  /* object-fit: contain; */
}
.imgAdaptive {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* object-fit: contain; */
}
