.clientsPage_wrapper {
  padding-top: 62px;
  position: relative;
}
.clients_wrapper {
  position: relative;
}
.client_wrapper {
  background-color: var(--color-3);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.client_link {
  /* border: solid 2px var(--color-2); */
  color: var(--color-2);
  padding: 1px 5px 1px 10px;
  display: flex;
  column-gap: 5px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-right: 5px;
  height: 40px;
  background-color: var(--color-5);
}
.admin .client_link {
  /* border: solid 2px var(--color-6); */
  color: var(--color-6);
}

.client_info_wrapper {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.client_info_block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 15px;
  gap: 5px;
}
.info_item {
  width: 15px;
  height: 15px;
  font-size: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* border-radius: 50%; */
  color: var(--color-3);
}

.info_item_groupAccompany {
  background-color: var(--color-1);
}
.info_item_individual {
  background-color: var(--color-6);
}
.info_item_calculated {
  background-color: var(--color-2);
}
.info_item_possibleÐ¡lient {
  background-color: var(--color-8);
}
.notClients {
  padding: 100px 20px;
  font-size: 30px;
  font-weight: 600;
  color: var(--color-1);
  text-align: center;
}
.not_added {
  font-size: 10px;
  white-space: nowrap;
  line-height: 1;
}
