.textarea_wrapper {
  position: relative;
  width: 100%;
  z-index: 3;
  border: solid 1px var(--color-1);
  margin-bottom: 20px;
}
.textarea_wrapper.big {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}
.textarea {
  width: 100%;
  height: 100%;
  resize: none;
  padding: 10px;
  margin: 0;
  border: none;
}
.big .textarea {
  padding: 20px;
  padding-top: 70px;
}
.textarea_btn {
  cursor: pointer;
  display: inline-block;
  position: absolute;
  right: 20px;
  bottom: 10px;
  width: 25px;
  height: 30px;

  border: solid 2px var(--color-1);
  border-radius: 5px;
  color: #fff;
}
.big .textarea_btn {
  right: 20px;
  top: 20px;
}
.textarea_btn_save {
  cursor: pointer;
  display: inline-block;
  position: absolute;
  right: 20px;
  top: 20px;
  background-color: var(--color-1);
  padding: 5px 10px;

  border-radius: 5px;
  color: #fff;
}
