.loader_fon {
  backdrop-filter: blur(1.5px);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.loader_wrapper {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 90px;
  height: 90px;
  /* background-color: var(--color-1); */
  border-radius: 50%;
  position: sticky;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  z-index: 2;
}

/* HTML: <div class="loader"></div> */
.loader {
  width: 15px;
  aspect-ratio: 1;
  border-radius: 50%;
  animation: l5 1s infinite linear alternate;
}
@keyframes l5 {
  0% {
    box-shadow: 20px 0 #000, -20px 0 #0002;
    background: #000;
  }
  33% {
    box-shadow: 20px 0 #000, -20px 0 #0002;
    background: #0002;
  }
  66% {
    box-shadow: 20px 0 #0002, -20px 0 #000;
    background: #0002;
  }
  100% {
    box-shadow: 20px 0 #0002, -20px 0 #000;
    background: #000;
  }
}
